/// <reference path="../definition/react.d.ts" />
/// <reference path="../definition/react-dom.d.ts" />
import React = require('react');
import ReactDOM = require('react-dom');

import w = require('../widget/widget');
import l = require('../widget/layout');

import likebutton = require('./likebutton');


class App extends React.Component<any, any>{
    visible = false
    constructor(props: any) {
        super(props);
        this.state = { clickCount: 0 };
    }
    componentDidMount(): void {
    }
    handleClick() {
        let c = this.state.clickCount + 1;
        this.setState({ clickCount: c });
        this.visible = !this.visible
    }
    render() {
        return (

            <l.Hlayout vflex={1} style={{ background: 'lightgreen', width: 600.65, marginLeft: 60.6, paddingLeft: 40.2, borderLeft: 'blue solid 20px', borderLeftWidth: 20 }} >
                
            </l.Hlayout>


        )
    }
}

ReactDOM.render(
    <App/>
    ,
    document.getElementById('content')
);